.PrivacyPolicyOUT {
    width: 100%;
    height: 100%;
    background-color: #333333;
    position: absolute;
    top:0;
    left:0;
    z-index: 999;
    animation: modal-bg-show 0.3s;
}
@keyframes modal-bg-show {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}
.PrivacyPolicy {
    width: 330px;
    height: calc(100% - 150px);
    z-index: 999;
    margin: 30px auto;
    background-color: #FFF;
    border: 1px solid black;
    border-radius: 8px;
    animation: modal-show 0.3s;
}
@keyframes modal-show {
    from {
      opacity: 0;
      margin-top: -50px;
    }
    to {
      opacity: 1;
      margin-top: 30px;
    }
}
.PrivacyPolicy .PrivacyPolicyContent {
    height: calc(100% - 60px);
    display: flex;
    padding: 20px 2px 20px 20px;
    flex-direction: column;
    align-items: flex-end;
}

.PrivacyPolicy .CloseModal {
    position: relative;
    font-size: 16px;
    font-weight: bold;
    color: #747474;
    margin-bottom: 20px;
    margin-right: 15px;
}
.PrivacyPolicy .CloseModal::after {
    content: "";
    position: absolute;
    left: -2px;
    bottom: -8px;
    height: 2px;
    width: 31px;
    background: #747474;
}
.PrivacyPolicy .ModalSection {
    margin-bottom: 25px;
}
.PrivacyPolicy .ModalTitle {
    font-size: 18px;
    font-weight: bold;
    color: #191919;
    margin-bottom: 25px;
}
.PrivacyPolicy .ModalSubTitle {
    font-size: 14px;
    font-weight: 600;
    color: #191919;
    margin-bottom: 10px;
}
.PrivacyPolicy .ModalSubTitle_2 {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 10px;
}

.PrivacyPolicy .ModalContent {
    font-size: 14px;
    margin-bottom: 15px;
    line-height: 1.25em;
}
.PrivacyPolicy .PrivacyPolicyText {
    padding-right: 15px;
    overflow-y: scroll;
    -ms-overflow-style: none; 
    scrollbar-width: none; 
}

.PrivacyPolicy .PrivacyPolicyText::-webkit-scrollbar {
    display: none;
}